.toggle-switch {
  position: relative;
  margin-right: 0px;
  width: 100px;
  display: inline-block;
  display: flex;
  align-items: center;
  
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  text-align: left; 
}

.toggle-switch-checkbox {
  display: none; }
.toggle-switch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 0 solid #bbb;
  border-radius: 20px;
  margin: 0; }
  .toggle-switch-label:focus {
    outline: none; }
    .toggle-switch-label:focus > span {
      box-shadow: 0 0 2px 5px red; }
  .toggle-switch-label > span:focus {
    outline: none; }

.toggle-switch-inner {
  
  display: block;
  width: 200%;
  margin-left: -100%;
  transition: margin 0.2s ease-in 0s; }
  .toggle-switch-inner:before, .toggle-switch-inner:after {
    display: block;
    float: left;
    width: 50%;
    height: 34px;
    padding: 0;
    line-height: 34px;
    font-size: 1.2rem;
    color: white;
    font-weight: bold;
    box-sizing: border-box; }
  .toggle-switch-inner:before {
    content: attr(data-yes);
    /*text-transform: uppercase;*/
    padding-left: 12px;
    background-color: #84be5d;
    color: #fff; }
  .toggle-switch-disabled {
    background-color: rgb(211, 211, 211);
    cursor: not-allowed; }
    .toggle-switch-disabled:before {
      background-color: rgb(211, 211, 211);
      cursor: not-allowed; }
  .toggle-switch-inner:after {
    content: attr(data-no);
    /*text-transform: uppercase;*/
    /**/padding-right: 19px; 
    background-color: #bbb;
    color: #fff;
    text-align: right; }
  .toggle-switch-switch {
    display: block;
    width: 24px;
    margin: 5px;
    background: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    /*right: 40px;*/
    /**/right: 65px;
    border: 0 solid #bbb;
    border-radius: 20px;
    transition: all 0.2s ease-in 0s; 
  }

.toggle-switch-checkbox:checked + .toggle-switch-label .toggle-switch-inner {
  margin-left: 0; }
.toggle-switch-checkbox:checked + .toggle-switch-label .toggle-switch-switch {
  /*right: 0px;*/
  right: 15px; }

.toggle-switch.small-switch {
  /*width: 40px; */
  width: 50px; }
  .toggle-switch.small-switch .toggle-switch-inner:after, .toggle-switch.small-switch .toggle-switch-inner:before {
    content: "";
    height: 20px;
    line-height: 20px; }
  .toggle-switch.small-switch .toggle-switch-switch {
    width: 16px;
    right: 20px;
    margin: 2px; }
    
@media screen and (max-width: 991px) {
  .toggle-switch {
    transform: scale(0.9); } }
@media screen and (max-width: 767px) {
  .toggle-switch {
    transform: scale(0.825); } }
@media screen and (max-width: 575px) {
  .toggle-switch {
    transform: scale(0.75); } }

